import ContainerComponent from "./ContainerComponent";
import { parse } from "marked";
export default class ImageComponent extends ContainerComponent {
    get defaultClasses() {
        return ["markdown-container"];
    }
    $mount() {
        super.$mount();
        const div = this.targetElement;
        const xhr = new XMLHttpRequest();
        xhr.onload = () => {
            const md = parse(xhr.responseText);
            this.targetElement.innerHTML = md;
        };
        xhr.open("GET", this.getAttribute("src"), false);
        xhr.send();
    }
    generateTag() {
        return document.createElement("div");
    }
}
ImageComponent.attributes = {
    src: {
        default: "",
        converter: "String"
    },
    inBuild: {
        converter: "Number",
        default: 0
    },
    outBuild: {
        converter: "Number",
        default: Number.MAX_SAFE_INTEGER
    },
    defaultContainer: {
        converter: "String",
        default: "div#paragraph-root"
    }
};



